<template>
  <div class="tagInput">
    <input
      ref="tagInput"
      class="tagInputInner"
      type="text"
      v-model="text"
      @click="handleClick"
      @input="handleInput"
      @select="handleSelect"
      @keydown="handleKeydown"
    />
    <div class="tagInputTagsWrap">
      <div
        class="tagInputTag"
        v-for="item in tagconfig"
        :key="item.id"
        @click="addTag(item)"
      >
        +{{ item.name }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Object,
      default: function() {
        return { text: "", tags: [] };
      }
    },
    tagconfig: {
      type: Array
    },
    leftSymbol: {
      type: String,
      default: "{"
    },
    rightSymbol: {
      type: String,
      default: "}"
    },
    prop: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  data() {
    return {
      text: "",
      // æææ ç­¾å¨ææ¬ä¸­çä½ç½®ä¿¡æ¯
      tagSelectionArr: [],
      // æ ç­¾ç»åä»¥åä½ç½®ä¿¡æ¯
      tags: [],
      textChangeByInput: false
    };
  },
  methods: {
    handleSelect(event) {
      let selectionStart = this.getCursortPosition();
      let rangeText = this.getSelectText();
      if (rangeText) {
        let config = this.isRangeInTags([
          selectionStart,
          selectionStart + rangeText.length
        ]);
        if (config.flag) {
          this.setCursorPosition(selectionStart);
        }
      }
    },
    handleInput(event) {
      this.textChangeByInput = true;
    },
    handleClick() {
      let selectionStart = this.getCursortPosition();
      let config = this.isCursorInTags(selectionStart);
      if (config.flag) {
        this.setCursorPosition(config.tag[1]);
      }
    },
    addTag(tag) {
      tag = Object.assign({}, tag);
      let selectionStart = this.getCursortPosition();
      let tagName = this.leftSymbol + tag.name + this.rightSymbol;
      let selectionEnd = selectionStart + tagName.length;
      tag.start = selectionStart;
      this.tags.push(tag);

      this.text =
        this.text.substr(0, selectionStart) +
        tagName +
        this.text.substr(selectionStart);
      // vueDomæ¸²ææ¯å¼æ­¥ï¼æä»¥è¦å¨ä¸ä¸ä¸ªçº¿ç¨è®¾ç½®åæ ä½ç½®
      this.$nextTick(() => {
        this.setCursorPosition(selectionEnd);
      });
    },
    // åæ å·¦å³ç§»å¨æ¶
    handleKeydown(event) {
      if (event.key === "ArrowRight" || event.key === "ArrowLeft") {
        setTimeout(() => {
          let selectionStart = this.getCursortPosition();
          let config = this.isCursorInTags(selectionStart);
          if (config.flag) {
            if (event.key === "ArrowRight") {
              this.setCursorPosition(config.tag[1]);
            }
            if (event.key === "ArrowLeft") {
              this.setCursorPosition(config.tag[0]);
            }
          }
        }, 0);
      }
    },
    // å¤æ­åæ æ¯å¦å¨tagä¸­ï¼ä¼ å¥åæ ä½ç½®posï¼è¿å{flag,tag}
    isCursorInTags(pos = 0) {
      let flag = false;
      let tag;
      this.tagSelectionArr.forEach(item => {
        if (pos > item[0] && pos < item[1]) {
          flag = true;
          tag = item;
        }
      });
      return { flag, tag };
    },
    // tange  ç±»åå¦([1,5])ï¼å¤æ­ä¸ä¸ªèå´åæ¯å¦å«ætagï¼è¿å{flag,tags}
    isRangeInTags(range) {
      let flag = false;
      let tags = [];
      this.tagSelectionArr.forEach(item => {
        if (
          (item[0] >= range[0] && item[0] < range[1]) ||
          (item[1] > range[0] && item[1] <= range[1])
        ) {
          flag = true;
          tags.push(item);
        }
      });
      // tags = tags.sort((a, b) => a[0] - b[0]);
      return { flag, tags };
    },
    // è·ååæ ä½ç½®
    getCursortPosition() {
      let cursorPos = 0;
      let textDom = this.$refs.tagInput;
      if (document.selection) {
        // IE Support
        textDom.focus();
        var selectRange = document.selection.createRange();
        selectRange.moveStart("character", -textDom.value.length);
        cursorPos = selectRange.text.length;
      } else if (textDom.selectionStart || textDom.selectionStart == "0") {
        // Firefox support
        cursorPos = textDom.selectionStart;
      }
      return cursorPos;
    },
    // è®¾ç½®åæ ä½ç½®
    setCursorPosition(pos, length = 0) {
      let end = pos + length;
      let textDom = this.$refs.tagInput;
      if (textDom.setSelectionRange) {
        textDom.focus();
        textDom.setSelectionRange(pos, end);
      } else if (textDom.createTextRange) {
        var range = textDom.createTextRange();
        range.collapse(true);
        range.moveEnd("character", end);
        range.moveStart("character", pos);
        range.select();
      }
    },
    // è·åéä¸­æå­
    getSelectText() {
      var sel = "";
      if (window.getSelection) {
        sel = window.getSelection();
      } else if (document.getSelection) {
        sel = document.getSelection();
      } else if (document.selection) {
        sel = document.selection.createRange();
      }
      return sel.toString();
    },
    validate() {
      if (this.prop.maxTags && this.tags.length > this.prop.maxTags) {
        this.valid = false;
        this.errorMsg = "æå¤å¯ä»¥æ·»å " + this.prop.maxTags + "ä¸ªæ ç­¾";
        this.errorKey = "maxTags";
        return;
      }
      if (this.prop.min && this.text.length < this.prop.min) {
        this.valid = false;
        this.errorMsg = "æå°" + this.prop.min + "ä¸ªå­ç¬¦ï¼è¯·æ­£ç¡®è¾å¥";
        this.errorKey = "min";
        return;
      }
      if (this.prop.max && this.text.length > this.prop.max) {
        this.valid = false;
        this.errorMsg = "æå¤" + this.prop.max + "ä¸ªå­ç¬¦ï¼è¯·æ­£ç¡®è¾å¥";
        this.errorKey = "max";
        return;
      }
      if (
        this.text.split(this.leftSymbol).length - 1 !== this.tags.length ||
        this.text.split(this.rightSymbol).length - 1 !== this.tags.length
      ) {
        this.valid = false;
        this.errorMsg = `è¯·å¿è¾å¥ â${this.leftSymbol}ââ${this.rightSymbol}â ç­ç¹æ®å­ç¬¦`;
        this.errorKey = "symbolError";
        return;
      }
      this.valid = true;
      this.errorMsg = null;
      this.errorKey = null;
    },
    // åæ­¥text
    syncText() {
      this.text = this.value.text || "";
      this.tags = this.value.tags || [];
      this.valid = this.value.valid || true;
      this.errorMsg = this.value.errorMsg || null;
      this.errorKey = this.value.errorKey || null;
      this.syncTag();
    },
    // åæ­¥tag
    syncTag() {
      let copyText = this.text;
      this.tags.forEach(tag => {
        let tagName = this.leftSymbol + tag.name + this.rightSymbol;
        let index = copyText.indexOf(tagName);
        tag.start = index;
        copyText = copyText.replace(tagName, "*".repeat(tagName.length));
      });
      this.tags.sort((a, b) => a.start - b.start);
      this.tagSelectionArr = this.tags.map(tag => {
        return [
          tag.start,
          tag.start +
            tag.name.length +
            this.leftSymbol.length +
            this.rightSymbol.length
        ];
      });
    }
  },
  watch: {
    text(newVal, oldVal) {
      let config;
      if (this.textChangeByInput && newVal.length < oldVal.length) {
        let newCursorPos = this.getCursortPosition();
        config = this.isCursorInTags(newCursorPos);
        this.textChangeByInput = false;
      }
      if (config && config.flag) {
        // å é¤æ ç­¾çæåµ
        this.tags = this.tags.filter(tag => {
          return tag.start !== config.tag[0];
        });
        this.text =
          this.text.substr(0, config.tag[0]) +
          this.text.substr(config.tag[1] - 1);
        // è®¾ç½®åæ ä½ç½®
        this.$nextTick(() => {
          this.setCursorPosition(config.tag[0]);
        });
        return;
      }
      this.syncTag();
      this.validate();
      this.$emit("input", {
        text: this.text,
        tags: this.tags,
        valid: this.valid,
        errorMsg: this.errorMsg,
        errorKey: this.errorKey
      });
    },
    "value.text"(val) {
      this.syncText();
    }
  },
  mounted() {
    this.syncText();
  }
};
</script>

<style lang="less" scoped>
.tagInput {
  display: inline-block;
  width: 100%;
  .tagInputInner {
    width: 100%;
    min-width: 300px;
    box-sizing: border-box;
    background: transparent;
    border: 1px solid #dadada;
    padding: 0 15px;
    outline: none;
    height: 32px;
    line-height: 32px;
    border-radius: 5px;
    font-size: 16px;
  }
  .tagInputTagsWrap {
    margin-top: 5px;
    font-size: 0;
    .tagInputTag {
      background-color: #ecf5ff;
      display: inline-block;
      height: 24px;
      padding: 0 8px;
      line-height: 22px;
      font-size: 12px;
      color: #409eff;
      border: 1px solid #d9ecff;
      border-radius: 4px;
      box-sizing: border-box;
      white-space: nowrap;
      cursor: pointer;
      & + .tagInputTag {
        margin-left: 10px;
      }
    }
  }
}
</style>
